import { Field } from 'formik';

import sprite from '../../../images/icons.svg';

import { BoxInputField, FieldLabel, InputField } from '../SecondStageForm/SecondStageForm.styled';
import {
  BoxPetAvatar,
  BoxRadioButtonSex,
  ContainerAvatar,
  ContainerButton,
  ContainerFormStepThird,
  ContainerInput,
  FieldAvatar,
  IconPlaceholder,
  IconSex,
  InputFieldTextArea,
  ParagraphPetAvatar,
  RadioButtonSex,
  ShowPlaceholderAvatar,
  TextArea,
  TextSpan,
} from './ThirdStageForm.styled';

const optionsSex = [
  {
    value: 'female',
    label: 'Female',
  },
  {
    value: 'male',
    label: 'Male',
  },
];

const heightTextarea = (currentRadioButton, checked) => {
  switch (checked) {
    case 'my-pet':
      return '79px';
    case 'sell':
      return '79px';
    default:
      return currentRadioButton !== 'my-pet' ? '182px' : '120px';
  }
};

const ThirdStageForm = ({
  showPlaceholder,
  setPreviewImage,
  setShowPlaceholder,
  previewImage,
  formik,
  setSelectedFile,
  currentRadioButton,
}) => {
  const handlePhotoChange = event => {
    const file = event.currentTarget.files[0];
    if (file) {
      setSelectedFile(file);
      setPreviewImage(URL.createObjectURL(file));
      setShowPlaceholder(false);
    } else {
      setSelectedFile(null);
      setShowPlaceholder(true);
      setPreviewImage('');
    }
    // formik.handleChange(event);
    // formik.setFieldValue('avatar', file);
  };

  return (
    <ContainerFormStepThird currentRadioButton={currentRadioButton !== 'my-pet'}>
      <ContainerButton>
        {currentRadioButton !== 'my-pet' && (
          <>
            <ParagraphPetAvatar>The sex</ParagraphPetAvatar>
            <BoxRadioButtonSex>
              {optionsSex.map(({ value, label }) => (
                <RadioButtonSex key={value} checked={formik.values.sex === value}>
                  <IconSex
                    className={value === 'male' ? 'male-icon' : 'female-icon'}
                    selectedGender={formik.values.sex}
                  >
                    <use href={`${sprite}#icon-${value}`}></use>
                  </IconSex>
                  {label}
                  <Field
                    key={`opt${value}`}
                    type="radio"
                    name={`sex`}
                    id={`opt${value}`}
                    value={value}
                    onChange={formik.handleChange}
                  />
                </RadioButtonSex>
              ))}
            </BoxRadioButtonSex>
          </>
        )}
        <ContainerAvatar currentRadioButton={currentRadioButton !== 'my-pet'}>
          <ParagraphPetAvatar currentRadioButton={currentRadioButton !== 'my-pet'}>
            Load the pet’s image:
          </ParagraphPetAvatar>
          <BoxPetAvatar>
            {showPlaceholder ? (
              <ShowPlaceholderAvatar>
                <IconPlaceholder>
                  <use href={`${sprite}#icon-plus`}></use>
                </IconPlaceholder>
              </ShowPlaceholderAvatar>
            ) : (
              <img src={previewImage} alt="Selected" style={{ width: '100%', height: '100%', objectFit: 'cover' }} />
            )}
            <FieldAvatar type="file" id="avatar" name="avatar" accept="image/*" onChange={handlePhotoChange} />
          </BoxPetAvatar>
        </ContainerAvatar>
      </ContainerButton>
      <ContainerInput>
        {currentRadioButton !== 'my-pet' && (
          <BoxInputField key="location">
            <FieldLabel htmlFor="location">Location</FieldLabel>
            <InputField
              type="text"
              id="location"
              name="location"
              autoComplete="off"
              placeholder="Type of location"
              value={formik.values.location}
              onChange={formik.handleChange}
            />
            {/* {formik.touched[name] && formik.errors[name] && <div style={{ color: 'red' }}>{formik.errors[name]}</div>} */}
          </BoxInputField>
        )}
        {currentRadioButton === 'sell' && (
          <BoxInputField key="price">
            <FieldLabel htmlFor="price">Price</FieldLabel>
            <InputField
              type="text"
              id="price"
              name="price"
              autoComplete="off"
              placeholder="Type of price"
              value={formik.values.price}
              onChange={formik.handleChange}
            />
            {/* {formik.touched[name] && formik.errors[name] && <div style={{ color: 'red' }}>{formik.errors[name]}</div>} */}
          </BoxInputField>
        )}
        <div>
          <TextSpan>Comments</TextSpan>
          {/* <InputFieldTextArea
            currentRadioButton={currentRadioButton}
            as={TextArea} // Використовуємо наші стилі для textarea
            id="comments"
            name="comments"
            placeholder="Type of pet"
            value={formik.values.comments}
            onChange={formik.handleChange}
          /> */}
          <InputFieldTextArea
            currentRadioButton={currentRadioButton}
            as={TextArea}
            id="comments"
            name="comments"
            placeholder="Type of pet"
            value={formik.values.comments}
            onChange={formik.handleChange}
            checked={currentRadioButton} // Додаємо параметр checked
            style={{ height: heightTextarea(currentRadioButton, formik.values.category) }} // Передаємо параметри до стилізації
          />
        </div>
      </ContainerInput>
    </ContainerFormStepThird>
  );
};

export default ThirdStageForm;
